.sidebar{
  flex: 1;
  min-height: 100vh;
  border-right: 0.5px solid rgb(223, 219, 219);
  background-color: white;


  .top{
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

    .logo{
      font-size: 20px;
      font-weight: bold;
      color: #6439ff;
    }

  }

  hr{
    height: 0;
    border: 0.5px solid rgb(223, 219, 219);
  }

  .center{
    padding-left: 10px;

    ul{
      list-style: none;
      margin: 0;
      padding: 0;

      .title{
        font-size: 10px;
        font-weight: bold;
        color: #999;
        margin: 15px 0 5px 0;
      }

      li{
        display: flex;
        align-items: center;
        padding: 5px;
        cursor: pointer;

        &:hover{
          background-color: #ece8ff;
        }

        .icon{
          font-size: 18px;
          color: #7451f8;
        }

        span{
          font-size: 13px;
          font-weight: 600;
          color: #888;
          margin-left: 10px;
        }
      }

    }
  }

  .bottom{
    display: flex;
    align-items: center;
    margin: 10px;

    .colorOption{
      width: 20px;
      height: 20px;
      border-radius: 5px;
      border: 1px solid #7451f8;
      cursor: pointer;
      margin: 5px;


      &:nth-child(1){
        background-color: whitesmoke;
      }
      &:nth-child(2){
        background-color: #333;
      }
      &:nth-child(3){
        background-color: darkblue;
      }
    }
  }

}